nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #fff;
  padding: 0 20px;
}

.nav-logo img {
  margin-top: 30px;
  width: 190px;
}

nav ul {
  display: flex;
  list-style: none;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

nav ul li {
  border-radius: 4px;
  transition: all 0.3s ease-in-out;
  padding-bottom: 5px;
}

nav ul li a {
  text-decoration: none;
  color: #333;
}

nav ul li:hover {
  background-color: #67f8c19a;
  color: #fff;
  padding: 5px;
}

/* media query */
@media (min-width: 768px) {
  nav ul {
    flex-direction: row;
  }
  nav ul li {
    padding: 10px;
  }
}
